export default {
	data() {
		return {
			active: false
		};
	},
	onLoad() {
		console.log('tab3')
	},
	onHide() {
		
	},
	methods: {
		backPage(){
			this.$Router.back();
		},
		chooseLocation(){
			uni.chooseLocation({
			    success:  (res) =>{
			        console.log('位置名称：' + res.name);
			        console.log('详细地址：' + res.address);
			        console.log('纬度：' + res.latitude);
			        console.log('经度：' + res.longitude);
			    },
				complete:(...res)=>{
					console.log(res)
				}
			});
		},
		getLocation(){
			uni.getLocation({
			    type: 'gcj02', //返回可以用于uni.openLocation的经纬度
			    success: function (res) {
			        const latitude = res.latitude;
			        const longitude = res.longitude;
			        uni.openLocation({
			            latitude: latitude,
			            longitude: longitude,
			            success: function () {
			                console.log('success');
			            }
			        });
			    }
			});
		},
		goToPage(url) {
			if (!url) return;
			uni.navigateTo({
				url
			});
		}
	}
};
